@tailwind base;
@tailwind components;
@tailwind utilities;

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  height: 100vh;
  width: 100vw;
}

.tour-main{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.heading{
  font-size: 3rem;
  margin: 3vh;
  border: 7px dashed blue;
  border-radius: 20px;
  padding: 1vh 5vw;
  font:bolder;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.cards{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  flex-wrap: wrap;
  max-width: 1300px;
}

.card{
    width: 400px;
    height:650px;
    padding: 1rem;
    margin: 1rem;
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    object-fit: cover;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: rgba(0,0,0,0.24) 0px 3px 8px;
}

.card:hover{
  background-color:rgb(233, 198, 204);
  transition: all 1s;

}

image{
  width: 380px;
  aspect-ratio: 1/1;
  object-fit: cover;
  border-radius: 20px;
}

.Tour-details{
  margin: 20px 5px;
  font:bolder;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.price{
  color:#1aff59;
  font-size: 1.3rem;
}

.name{
  color:black;
  font-size: 1.3rem;
}

.read-more{
  color: #1aff59;
}

.read-more:hover{
  cursor: pointer;
}


.btn{
  margin-top: 18px;
  padding: 10px 80px;
  border: 1px solid #b4161b;
  border-radius: 10px;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
  background-color:#b4161b21 ;
}

.btn:hover{
  background-color: red;
  color: white;
  transition: all 0.2s;
}

.refresh{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 20px;
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.txt-tour{
  font-size: 20px;
}

.refresh-btn{
  width: 100px;
  height: 50px;
  background-color: grey;
  color: red;
  border-radius: 5px;
  margin-top: 3px;
}

.refresh-btn:hover{
  color: black;
  background-color:wheat;
}